
.wrapper{width:200px;margin:10px;padding:10px 0;border:1px solid rgba(0,0,0,0)
}
.wrapper:hover{border:1px solid rgba(255,255,255,0.3)
}
.left{float:left;height:20px
}
.left .label{display:inline-block;height:100%;vertical-align:middle;text-align:center;color:#FFFFFF;float:left;font-size:10px;padding:0 3px;width:40px
}
.left .label p{float:right;margin:0;margin-top:3px
}
.left .slider{height:20px;margin:0;width:70px
}
.right .text{float:left
}
.right .text input{float:left;border:none;padding:0;color:#333333;width:26px;height:20px;font-size:10px;line-height:0;vertical-align:middle;text-align:center;margin:0 2px
}
.right .buttons input{width:10px;height:10px;line-height:0;border:none;padding:0;display:block;font-size:10px;color:#333333
}
.right .buttons input:hover{background-color:#999
}


